import React from 'react';
import { ChevronLeft } from 'react-bootstrap-icons'; 

class ForgotForm extends React.Component {
    constructor() {
        super();
        this.state = {};
    }

    render() {
        return (
            <form className='popupForm'>

                <div className='popupFormNav'>
                    <button className='iconBtn' onClick={() => this.props.changeRoute('menu')}>
                        <ChevronLeft className='icon' size={20} /> Back
                    </button>
                </div>

                <div className='flexCenter'>
                    <div className='flexCenter placeholderImg'>
                        Logo
                    </div>

                    <h3 className='loginScreenFormTitle' >Forgot Username/ Password</h3>

                    <div>
                        <label>Email Address:</label><br />
                        <input name='recoveryEmail' type='text' />
                    </div>

                    <p className='invalidText'>Invalid email address.</p>

                    <input className='formSubmitBtn' type='submit' value='Send Email' />
                </div>

            </form>
        )
    }
}

export default ForgotForm;